.contact {
  height: 100vh;
  position: relative;
}
.contact__bg {
  width: 20px;
  height: 100%;
  background-color: #636e72;
  position: absolute;
}
.contact__wrapper {
  padding: 50px;
  display: flex;
}
.contact__left {
  margin-top: 10%;
  flex: 1;
}
.contact__right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10%;
  justify-content: center;
}
.contact__title {
  font-size: 45px;
  width: 80%;
}
.contact__info__item {
  display: flex;
  align-items: center;
  margin: 50px 0px;
  font-weight: 300;
  width: 70%;
}
.contact__icon {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}
.contact__desc {
  font-weight: 300;
}
.contact__desc b {
  font-weight: 800;
}
form {
  margin-top: 20px;
}

input {
  width: 51%;
  height: 50px;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}
textarea {
  width: 100%;
  margin: 10px 0px;
  font-size: 14px;
  padding-left: 10px;
}
button {
  width: 100px;
  padding: 15px;
  font-weight: 500;
  cursor: pointer;
  border: none;
}

@media screen and (max-width: 480px) {
  .contact__wrapper {
    flex-direction: column;
  }
  .contact__wrapper {
    flex-direction: column;
    padding: 0px 50px;
  }
  .contact__title {
    font-size: 30px;
  }
  .contact__info__item {
    margin: 20px 0px;
    width: 100%;
  }
  .contact__desc {
    display: none;
  }
  form {
    margin-top: 0;
    display: flex;
    flex-wrap: wrap;
  }
  input {
    width: 35%;
    height: 40px;
    margin: 10px;
    margin-left: 0px;
  }
  button {
    margin-top: 10px;
  }
}
