.productlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 100px;
  text-align: center;
  height: 100vh;
}

.productlist__text {
  width: 65%;
}
.productlist__title {
  font-size: 50px;
  font-weight: 600;
}
.productlist__desc {
  margin: 20px 0px;
}
.productlist___list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (max-width: 480px) {
  .productlist {
    padding: 50px 10px 10px 10px;
  }
  .productlist__text {
    width: 100%;
  }
  .productlist__desc {
    display: none;
  }
}
