.about {
  height: 100vh;
  display: flex;
  align-items: center;
}
.about__left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}
.about__right {
  flex: 1;
  padding-bottom: 85px;
}
.about__card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}

.about__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: #333;
}
.btn-highlighted {
  margin: 65px 0px 0px 0;
  position: absolute;
  display: flex;
  width: 130px;
  justify-content: center;
}
.btn-highlighted:hover {
  margin: 65px 0px 0px 0;
  position: absolute;
  background-color: #636e72;
  color: #ecf0f1;
  transform: scale(1.05);
  display: flex;
  transition-duration: 0.3s;
}
.about__title {
  font-weight: 400;
}
.about__sub {
  margin: 20px 0px;
}
.about__desc {
  font-weight: 300;
  width: 85%;
}

@media screen and (max-width: 480px) {
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 50px;
  }
  .about__left {
    width: 100%;
  }
  .about__card {
    height: 30vh;
  }
  .about__card.bg {
    display: none;
  }
  .about__right {
    align-items: center;
    padding: 20px;
  }
  p.about__desc {
    margin-top: 20px;
    margin-left: 25px;
  }
  .btn-highlighted {
    margin-top: -5px;
    margin-left: 27%;
    background-color: #636e72;
  }
  .btn-highlighted:hover {
    margin-top: -5px;
    margin-left: 27%;
    background-color: #636e72;
  }
}
