.product {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(131, 149, 167);
  border-radius: 10px 10px 0 0;
  overflow: hidden;
}
.product__browser {
  height: 20px;
  background-color: rgb(129, 236, 236);
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
}
.product__circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.product__img {
  width: 100%;
  transition: all 10s ease;
}
.product:hover .product__img {
  transform: translateY(-100%);
}

@media screen and (max-width: 480px) {
  .product {
    width: 40%;
    height: 20vh;
  }
}
