.intro {
  display: flex;
  height: 100vh;
}

.intro__left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.intro__right {
  flex: 1;
  position: relative;
}
.intro__left__wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.intro__intro {
  font-size: 30px;
  font-weight: 300;
}
.intro__name {
  font-size: 60px;
}
.intro__title {
  height: 50px;
  overflow: hidden;
}
.intro__title__item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #636e72;
  display: flex;
  align-items: center;
}
.intro__title__wrapper {
  height: 100%;
  animation: move 8s ease-in-out infinite alternate;
}
@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
  100% {
    transform: translateY(-200px);
  }
}
.intro__img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  position: absolute;
}
.intro__img__bg {
  clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #636e72;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}
.intro__scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;
  }
  .intro__left__wrapper {
    padding: 10px;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
  }
  .intro__desc {
    display: none;
  }
}
